@import url("https://fonts.googleapis.com/css?family=Zeyada");

body {
  /* width: 100vw; */
  height: 100vw;
  margin: 10px;
}

* {
  margin: 0px;
  box-sizing: border-box;
  /* width: 100vw; */
}
.Nav {
  display: flex;
  width: 100vw;
  flex-direction: row;
  justify-content: space-between;
  /* border-style: solid; */
  border-width: 1px;
  padding-left: 110px;
  padding-right: 100px;
  border-color: rgba(0, 0, 0, 0.1);
}

.polyimg {
  box-sizing: border-box;
  color: #0126a8;
  /* height: 35px; */
}
.ravimg {
  font-family: "Zeyada";
  font-style: normal;
  font-weight: normal;
  font-size: 51px;
  line-height: 75px;
  color: #0126a8;
}
.vector {
  height: 54px;
  width: 50px;
  color: #0126a8;
}
.logoutbutton {
  padding: 5px 10px 5px 10px;
  border-radius: 10px;
  font-style: normal;
  background-color: #0126a8;
  color: white;
}
.logout {
  padding-top: 10px;
}
