.imgchatbox {
  border-radius: 50%;
  height: 45px;
  margin-top: -20px;
  border: 2px solid black;
}
.chatboxbox {
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
}
.midchatboxbox {
  display: flex;
  flex-direction: row;
  /* width: 470px; */
  /* width: 30%; */
  /* height: 100vh; */
}
.midchatboxboxleft {
  width: 25%;
  /* border: 2px solid yellow; */
  background: #f7f7f7;
  position: fixed;
  min-height: 90vh;
}
.midchatboxboxleft p {
  text-align: left;
  margin: 5% 2%;
  font-family: Lato;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  /* identical to box height */

  color: #000000;
}
.msgschatbox {
  display: flex;
  flex-direction: column;
  /* margin: 110px 0; */
  /* overflow-y: scroll; */
  /* height: 90vh; */
  min-height: 90vh;
  margin: 5vh 0 5vh;
  width: 70%;
  margin-left: 25%;
  /* position: fixed; */
}
.msgschatbox p {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: 500;
  font-size: 25px;
  margin: 20px;
  /* margin-top: 10px;
  margin-left: 10px;

  margin-right: 10px; */
  overflow-wrap: break-word;
  /* width: 1050px; */
  /* width: 70%; */
  max-width: 400px;
}

.msgchatbox {
  display: flex;
  /* padding: 20px 10px 0 20px; */
  padding: 0px 20px 0px 10px;
  margin: 20px;
  border-radius: 3000px;
  box-shadow: 0 0 10px rgb(164, 164, 164);
  align-items: center;
}
.sentchatbox {
  background-color: #395dff;
  color: white;
  border-top-right-radius: 1000px;
  flex-direction: row-reverse;
  /* padding: 20px 20px 0 10px; */
  /* padding: 10px 30px 10px 10px; */
  padding: 0px 20px 0px 10px;
  text-align: end;
  float: right;
}

.receivedchatbox {
  border: 1px solid lightgray;
  background-color: #fafafa;
  border-top-left-radius: 1000px;
  float: left;
}
.msgschatbox img {
  border-radius: 50%;
  height: 45px;
  /* margin-top: -20px; */
  border: 2px solid black;
}

.sendMsgchatbox {
  position: fixed;
  display: flex;
  width: 100%;
  bottom: 0;
  z-index: 1;
  border-top: 1px solid lightgray;
  /* margin-left: -5px; */
  /* padding: 10px; */
  padding-bottom: 30px;
  background-color: #fafafa;
}

