@media (max-width: 775px) {
  p {
    font-size: 20px;
  }
  .sentchatbox {
    padding: 10px 10px 0 10px;
  }
  .receivedchatbox {
    padding: 10px 10px 0 10px;
  }
  imgchatbox {
    height: 35px;
    margin-top: -10px;
  }
}

@media (max-width: 500px) {
  p {
    font-size: 15px;
  }
  .sentchatbox {
    padding: 7px 7px 0 7px;
  }
  .receivedchatbox {
    padding: 7px 7px 0 7px;
  }
  imgchatbox {
    height: 30px;
    margin-top: -7px;
  }
}
